/*
 * Footer
 *
 * Separated section of content at the bottom of all pages, save the homepage.
 */
.bs-docs-footer {
  padding-top: 25px;
  padding-bottom: 20px;
  margin-top: 10px;
  color: #F2E5DE;
  text-align: center;
  /*
   * Social buttons
   *
   * Twitter and GitHub social action buttons (for homepage and footer).
   */
  /* Style the GitHub buttons via CSS instead of inline attributes */
}
.bs-docs-footer .bs-docs-footer-links {
  padding-left: 0;
  margin-top: 20px;
  color: #999;
}
.bs-docs-footer .bs-docs-footer-links li {
  display: inline;
  padding: 0 2px;
}
.bs-docs-footer .bs-docs-footer-links li:first-child {
  padding-left: 0;
}
@media (min-width: 768px) {
  .bs-docs-footer .bs-docs-footer p {
    margin-bottom: 0;
  }
}
.bs-docs-footer .bs-docs-social {
  margin-bottom: 20px;
  text-align: center;
}
.bs-docs-footer .bs-docs-social-buttons {
  display: inline-block;
  padding-left: 0;
  margin-bottom: 0;
  list-style: none;
}
.bs-docs-footer .bs-docs-social-buttons li {
  display: inline-block;
  padding: 0px;
  line-height: 1;
}
.bs-docs-footer .bs-docs-social-buttons .twitter-follow-button {
  width: 225px !important;
}
.bs-docs-footer .bs-docs-social-buttons .twitter-share-button {
  width: 98px !important;
}
.bs-docs-footer .github-btn {
  overflow: hidden;
  border: 0;
}
